function findCoveredTiles(actor, tiles) {
    return tiles.filter(function (tile) {
        var horizontalValid = tile.x < actor.x && tile.x + tile.width > actor.x ||
            tile.x < actor.x + actor.width && tile.x + tile.width > actor.x + actor.width;
        var verticalValid = tile.y < actor.y && tile.y + tile.height > actor.y ||
            tile.y < actor.y + actor.height && tile.y + tile.height > actor.y + actor.height;
        return horizontalValid && verticalValid;
    });
}
export function isCollision(actor1, actor2) {
    var xMiddle = actor1.x + actor1.width / 2;
    var yMiddle = actor1.y + actor1.height / 2;
    var horizontalValid = xMiddle > actor2.x && xMiddle < actor2.x + actor2.width;
    var verticalValid = yMiddle > actor2.y && yMiddle < actor2.y + actor2.height;
    return horizontalValid && verticalValid;
}
export function findTilesWithCoveredMiddle(actor, tiles) {
    return tiles.filter(function (tile) {
        var xMiddle = tile.x + tile.width / 2;
        var yMiddle = tile.y + tile.height / 2;
        var horizontalValid = xMiddle > actor.x && xMiddle < actor.x + actor.width;
        var verticalValid = yMiddle > actor.y && yMiddle < actor.y + actor.height;
        return horizontalValid && verticalValid;
    });
}
export function getMaxUp(actor, map) {
    var coveredTiles = findCoveredTiles(actor, map.tiles);
    var upTiles = coveredTiles.filter(function (tile) { return tile.y === coveredTiles[0].y; });
    return Math.min.apply(Math, upTiles.map(function (tile) {
        var space = actor.y - tile.y;
        var aboveTiles = map.tiles.filter(function (el) { return el.x === tile.x && el.y < tile.y; });
        for (var i = aboveTiles.length - 1; i >= 0; i--) {
            if (aboveTiles[i].isWall) {
                break;
            }
            space = space + aboveTiles[i].height;
        }
        return space;
    }));
}
export function getMaxDown(actor, map) {
    var coveredTiles = findCoveredTiles(actor, map.tiles);
    var downTiles = coveredTiles
        .filter(function (tile) { return tile.y === coveredTiles[coveredTiles.length - 1].y; });
    return Math.min.apply(Math, downTiles.map(function (tile) {
        var space = tile.y + tile.height - (actor.y + actor.height);
        var belowTiles = map.tiles.filter(function (el) { return el.x === tile.x && el.y > tile.y; });
        for (var i = 0; i < belowTiles.length; i++) {
            if (belowTiles[i].isWall) {
                break;
            }
            space = space + belowTiles[i].height;
        }
        return space;
    }));
}
export function getMaxLeft(actor, map) {
    var coveredTiles = findCoveredTiles(actor, map.tiles);
    var leftTiles = coveredTiles.filter(function (tile) { return tile.x === coveredTiles[0].x; });
    return Math.min.apply(Math, leftTiles.map(function (tile) {
        var space = actor.x - tile.x;
        var beforeTiles = map.tiles.filter(function (el) { return el.y === tile.y && el.x < tile.x; });
        for (var i = beforeTiles.length - 1; i >= 0; i--) {
            if (beforeTiles[i].isWall) {
                break;
            }
            space = space + beforeTiles[i].width;
        }
        return space;
    }));
}
export function getMaxRight(actor, map) {
    var coveredTiles = findCoveredTiles(actor, map.tiles);
    var rightTiles = coveredTiles
        .filter(function (tile) { return tile.x === coveredTiles[coveredTiles.length - 1].x; });
    return Math.min.apply(Math, rightTiles.map(function (tile) {
        var space = tile.x + tile.width - (actor.x + actor.width);
        var afterTiles = map.tiles.filter(function (el) { return el.y === tile.y && el.x > tile.x; });
        for (var i = 0; i < afterTiles.length; i++) {
            if (afterTiles[i].isWall) {
                break;
            }
            space = space + afterTiles[i].width;
        }
        return space;
    }));
}
